export default {
	data() {
		return {
			status: "loading",
			searchVal: '',
			canSearch: true,
			timer: null
		}
	},
	methods: {
		searchCancel() {
			this.searchVal = "";
			uni.hideKeyboard();
		},
		constantlySearch(e) {
			const that = this;
			clearTimeout(that.timer);
			that.timer = setTimeout(function() {
				console.log(e.detail.value)
			}, 800);
		},
		selectItem(id, name) {
			const that = this;
			var pages = getCurrentPages(),
				prevPage = null;
			if (pages.length > 1) {
				prevPage = pages[pages.length - 2];
			}
			if (prevPage) {





				prevPage.$vm.userId = id;
				prevPage.$vm.userName = name

			}
			setTimeout(() => {
				that.$Router.back(1);
			}, 100)
		}
	}
}
